import {AfterViewChecked, AfterViewInit, Component, ElementRef, OnInit} from '@angular/core';
import {ViewEncapsulation} from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent implements OnInit, AfterViewInit {
  title = 'flower-builder-ui';
  grid: string;
  gridItems = [];
  stateMap = {};
  down = false;
  create = true;

  constructor(private elementRef: ElementRef) {
  }

  ngOnInit(): void {
    this.grid = this.generateGrid();
  }

  private generateGrid(): string {
    let res = '<div class="col">';
    const rowMargin = 'row-margin';
    const colMargin = 'col-margin';
    for (let i = 0; i < 21; i++) {
      res += `<div class="row  ${i !== 0 ? rowMargin : ''}">`;
      for (let j = 0; j < (i % 2 === 0 ? 22 : 23); j++) {
        const id = 'id_' + i + '_' + j;
        const div = `<div id="${id}" class="hexagon  ${j !== 0 ? colMargin : ''}"></div>`;
        this.gridItems.push('#' + id);
        this.stateMap[id] = false;
        res += div;
      }
      res += '</div>';
    }
    return res + '</div>';
  }

  updateBorder(id) {
    const classes = this.elementRef.nativeElement.querySelector(id).classList;

    if (!this.stateMap[id]) {
      classes.remove('hexagon');
      classes.add('hexagon-selected');
    } else {
      classes.remove('hexagon-selected');
      classes.add('hexagon');
    }
    this.stateMap[id] = !this.stateMap[id];
  }

  setSelected(id) {
    if (!this.down) {
      return;
    }
    if (this.create && !this.stateMap[id]) {
      const classes = this.elementRef.nativeElement.querySelector(id).classList;
      classes.remove('hexagon');
      classes.add('hexagon-selected');
      this.stateMap[id] = true;
    } else if (!this.create && this.stateMap[id]) {
      const classes = this.elementRef.nativeElement.querySelector(id).classList;
      classes.remove('hexagon-selected');
      classes.add('hexagon');
      this.stateMap[id] = false;
    }
  }

  ngAfterViewInit() {
    for (const id of this.gridItems) {
      if (this.elementRef.nativeElement.querySelector(id)) {
        // this.elementRef.nativeElement.querySelector(id).addEventListener('click', () => this.updateBorder(id));
        this.elementRef.nativeElement.querySelector(id).addEventListener('mouseenter', () => this.setSelected(id));
        this.elementRef.nativeElement.querySelector(id).addEventListener('mousedown', () => this.mouseDown(id));
        this.elementRef.nativeElement.querySelector(id).addEventListener('mouseup', () => this.mouseUp(id));
      }
    }
  }

  private mouseDown(id: string) {
    if (this.stateMap[id]) {
      this.create = false;
    }
    console.log('down');
    this.down = true;
    this.setSelected(id);
  }

  private mouseUp(id: string) {
    console.log('up');
    this.down = false;
    this.create = true;
  }
}
